html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  background-color: #f0f0f0;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  margin: 0 0 2rem;
  line-height: 1.5;
}

h2 {
  margin: 0 0 2rem;
  line-height: 1.5;
}

h3 {
  margin: 0 0 2rem;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: #111;
}

.header {
  position: relative;
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../assets/img/header_bg.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .header {
    padding-bottom: 15rem;
  }
}
.header__top-bar {
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
}
@media (min-width: 768px) {
  .header__top-bar {
    padding: 0;
  }
}
.header__logo {
  width: 22rem;
}
@media (min-width: 768px) {
  .header__content {
    margin-top: 10rem;
    max-width: 60rem;
  }
}
.header__heading {
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__text {
  color: #fff;
  display: none;
}
@media (min-width: 768px) {
  .header__text {
    display: block;
  }
}
.header__picture {
  display: none;
}
@media (min-width: 768px) {
  .header__picture {
    display: block;
    position: absolute;
    right: 0;
    bottom: -5rem;
    max-width: 35rem;
  }
}
@media (min-width: 1280px) {
  .header__picture {
    max-width: 60rem;
  }
}
.header__img {
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 5rem;
}

.properties {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .properties {
    margin-top: 10rem;
  }
}
.properties__heading {
  font-weight: 400;
}
.properties__heading span {
  font-weight: 700;
}
.properties__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.properties__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .properties__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.featured {
  margin-top: 5rem;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../assets/img/destacada.jpg);
  background-size: 100%, cover;
  background-position: center, -50rem -25rem;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .featured {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../assets/img/destacada.jpg);
    background-position: center, -40rem center;
  }
}
.featured__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .featured__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.featured__content {
  padding: 4rem;
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .featured__content {
    padding: 10rem 5rem;
    grid-column: 2/3;
  }
}
.featured__heading {
  text-transform: uppercase;
  color: #fff;
}
.featured__text {
  font-size: 1.8rem;
  color: #fff;
}
.featured__price {
  font-size: 2.4rem;
  font-weight: 700;
  color: #fff;
}
.featured__btn {
  display: block;
  margin-top: 2rem;
  padding: 1rem 2rem;
  background-color: #fff;
  font-size: 2rem;
  font-weight: 700;
  color: #3b4862;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border: none;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .featured__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .featured__btn:hover {
    background-color: #e6e6e6;
    cursor: pointer;
  }
}

.testimonials-blog {
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .testimonials-blog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 1024px) {
  .testimonials-blog__blog {
    grid-column: 2/4;
  }
}
.testimonials-blog__heading {
  font-weight: 400;
}
.testimonials-blog__heading span {
  font-weight: 700;
  display: block;
}
.testimonials-blog__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}

.footer {
  margin-top: 5rem;
  padding-top: 5rem;
  background-color: #3b4862;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__box {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__heading {
  font-size: 2.2rem;
  color: #fff;
  text-transform: uppercase;
}
.footer__text, .footer__email, .footer__phone {
  color: #fff;
}
.footer__copyright {
  padding: 2rem;
  background-color: #283142;
  color: #fff;
  text-align: center;
  margin: 5rem 0 0;
}

.hamburguer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  margin: 0;
  padding: 0;
  color: #fff;
  border: none;
}
@media (min-width: 768px) {
  .hamburguer {
    display: none;
  }
}
.hamburguer__icon {
  text-align: center;
  color: #fff;
  height: 3rem;
}

.nav {
  margin-top: 2.5rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
}
@media (min-width: 768px) {
  .nav {
    gap: 2rem;
    margin-top: 0;
    width: auto;
  }
}
.nav--header {
  height: 0;
  overflow: hidden;
  pointer-events: none;
  transition: 0.2s all ease-in-out;
}
@media (min-width: 768px) {
  .nav--header {
    flex-flow: row;
    overflow: visible;
    pointer-events: auto;
  }
}
.nav--active {
  height: 30vh;
  pointer-events: auto;
}
.nav__link {
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 1.8rem;
  border-bottom: 0.1rem solid transparent;
  transition: all 0.2s ease;
}
@media (hover: hover) {
  .nav__link:hover {
    border-color: #fff;
  }
}
@media (min-width: 768px) {
  .nav__link--active {
    border-color: #fff;
  }
}
.nav__link--footer {
  text-align: left;
}

.property {
  background-color: #fff;
  border-radius: 1rem;
}
.property__bg {
  aspect-ratio: 10/6;
  background-size: auto 150%;
  position: relative;
}
.property:nth-child(1) .property__bg {
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.7) 100%), url("../assets/img/propiedad_1.jpg");
}
.property:nth-child(2) .property__bg {
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.7) 100%), url("../assets/img/propiedad_2.jpg");
}
.property:nth-child(3) .property__bg {
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.7) 100%), url("../assets/img/propiedad_3.jpg");
}
.property:nth-child(4) .property__bg {
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.7) 100%), url("../assets/img/propiedad_4.jpg");
}
.property:nth-child(5) .property__bg {
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.7) 100%), url("../assets/img/propiedad_5.jpg");
}
.property:nth-child(6) .property__bg {
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.7) 100%), url("../assets/img/propiedad_6.jpg");
}
.property__name {
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  color: #fff;
  margin: 0;
  font-size: 2.4rem;
}
.property__content {
  padding: 2rem;
}
.property__desc {
  margin: 0 0 1rem;
}
.property__price {
  font-size: 2.4rem;
  font-weight: 700;
  color: #3b4862;
  margin: 0;
}
.property__btn {
  display: block;
  margin-top: 2rem;
  padding: 1rem 2rem;
  background-color: #3b4862;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border: none;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .property__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .property__btn:hover {
    background-color: #283142;
    cursor: pointer;
  }
}

.testimonial {
  text-align: center;
}
.testimonial__img {
  margin: 0 auto;
  width: 20rem;
  border-radius: 1rem;
}
.testimonial__content {
  margin-top: -10rem;
  padding: 10rem 2rem 2rem;
  background-color: #fff;
  border-radius: 1rem;
}
.testimonial__name {
  font-size: 2.4rem;
  font-weight: 700;
  margin: 1rem 0 0;
}
.testimonial__text {
  margin: 0;
}
.testimonial__text::before {
  content: "";
  background-image: url(../assets/img/quote.svg);
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  width: 4rem;
  height: 4rem;
}

.blog-entry {
  background-color: #fff;
  margin-bottom: 2rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.blog-entry__picture {
  grid-column: 1/3;
}
.blog-entry__img {
  height: 30rem;
  object-fit: cover;
  width: 100%;
}
.blog-entry__content {
  padding: 1rem;
}
@media (min-width: 768px) {
  .blog-entry__content {
    grid-column: 3/6;
  }
}
.blog-entry__meta {
  font-size: 1.5rem;
  margin: 0;
}
.blog-entry__text {
  margin: 1rem 0 0;
}
.blog-entry__link {
  display: block;
  margin-top: 2rem;
  padding: 1rem 2rem;
  background-color: #3b4862;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border: none;
  border-radius: 1rem;
  text-transform: none;
}
@media (min-width: 768px) {
  .blog-entry__link {
    width: auto;
  }
}
@media (hover: hover) {
  .blog-entry__link:hover {
    background-color: #283142;
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  .blog-entry__link {
    display: inline-block;
  }
}